import { Component, Fragment } from "react";
import '../css/article.css'


export class Blog extends Component{
    constructor(props){
        super(props)
        this.state = {
            ImgFile:null,
            Header:null,
            Body:null,
            openModel:this.props.openModel,
            articles:null,
            isLoaded:false,
            RenderArticle:false,
        }
    }

    async componentDidMount(){
        let data = await fetch("http://localhost:5000/article/get_articles")
        data = await data.json()
        this.setState({
            articles:data,
            isLoaded:true
        })
    }
    
    

    async Prevent(e){
        e.preventDefault()
        const user_id = JSON.parse(localStorage.getItem("currentUser")).user_id
        const fd = new FormData()
        fd.append('image',this.state.ImgFile,this.state.ImgFile.name)
        fd.append('title',e.target[1].value)
        fd.append('body',e.target[2].value)
        fd.append('user_id',user_id)
        let request = await fetch("http://localhost:5000/article/register_article",{
            method:"POST",
            body:fd
        })
        if(request.status === 200){
            alert("Articulo registrado exitosamente!")
            let data = await fetch("http://localhost:5000/article/get_articles")
            data = await request.json()
            this.setState({
                openModel:0,
                articles:data
            })
            e.target[1].value = ""
            e.target[2].value = ""
        }
        else{
            alert("Error registrando el post :(")
        }
        
    }

    readHandler(e){
        this.setState({
            ImgFile:e.target.files[0]
        })

    }

    GenerateForm(){
        return(
            <form className="article-form" onSubmit={e => this.Prevent(e)} method="POST">   
                <div className="form-input-container">
                    <label htmlFor="file-input">Ingrese banner del articulo</label> 
                    <input required name="file-input" id="file-input"  onChange={e=> this.readHandler(e)} type="file" accept="image/*"/>
                </div>
                <div className="form-input-container">
                    <label htmlFor="article-title">Titulo del articulo</label>
                    <input required  type="text" name="article-title" id="article-title"/>
                </div>
                <div className="form-input-container">
                    <label htmlFor="article-body">Contenido del articulo</label>
                    <textarea required  id="article-body" name="article-body"></textarea>
                </div>
                <button>Crear Articulo</button>
            </form>
        );
    }

    openArticleForm(){
        this.setState({
            openModel:1
        })
    }
    renderSpecificArticle(e,article){
        this.setState({
            RenderArticle:article
        })
    }

    //original <img alt="" src={require(`../user_images/${article.username}/${article.img_url}`)} className="article-img"/>
    renderArticle(article){
        return(
            <div className="article" key={article.article_id}>
                <img alt="" src={require(`../../public/user_images/${article.username}/${article.img_url}`)} className="article-img"/>
                <div className="article-text-container">
                    <h1 className="article-title">{article.title}</h1>
                    <h2 className="article-autor">{article.username}</h2>
                </div>
                <div className="article-button-container">
                    <button onClick={e => this.renderSpecificArticle(e,article)}  className="show-article">Ver articulo</button>
                </div>
            </div>
        )
    }
    ShowArticles(){
        const user = JSON.parse(localStorage.getItem("currentUser"))
        const articles = this.state.articles
        if(user != null){
            if(this.state.isLoaded){
                return(
                    <Fragment>
                        <div id="show-article-container">
                            <button onClick={e => this.openArticleForm()} id="button-open-form" className="show-article">Crear Articulo</button>
                        </div>
                        <div className="article-container">
                            {articles.map((article) => this.renderArticle(article))}
                        </div>
                       
                    </Fragment>
                );
            }
            return;
            
        }
        if(this.state.isLoaded){
            return(
                <Fragment>
                    <div className="article-container">
                            {articles.map((article) => this.renderArticle(article))}
                    </div>
                </Fragment>
            );
        }
        
    }

    async deleteArticle(e,RenderArticle){
        let request = await fetch(`http://localhost:5000/article/delete_article/${RenderArticle.article_id}`,{
            method:'DELETE',
        })
        if(request.status === 200){
            let data = await fetch("http://localhost:5000/article/get_articles")
            data = await data.json()
            this.setState({
                articles:data,
                isLoaded:true,
                RenderArticle:null
            })
        }
        else{
            console.log("No borrado")
        }
        
    }

    ShowArticle(){
        const RenderArticle = this.state.RenderArticle
        const user = JSON.parse(localStorage.getItem("currentUser"))
        if(user != null){
            return(
                <Fragment>
                    <div id="show-article-container">
                        <button onClick={e => this.deleteArticle(e,RenderArticle)} id="button-open-form" className="show-article">Borrar Articulo</button>
                    </div>
                    <div className="article-container-main">
                        <p className="article-autor">Autor: {RenderArticle.username}</p>
                        <div className="img-container">
                            <img alt="" src={require(`../user_images/${RenderArticle.username}/${RenderArticle.img_url}`)}/>
                        </div>
                        <h1 className="article-container-main-title">{RenderArticle.title}</h1>
                        <pre>{RenderArticle.body}</pre>
                    </div>
                </Fragment>
            );
        }
        else{
            return(
                <Fragment>
                    <div className="article-container-main">
                        <p className="article-autor">Autor: {RenderArticle.username}</p>
                        <div className="img-container">
                            <img alt="" src={require(`../user_images/${RenderArticle.username}/${RenderArticle.img_url}`)}/>
                        </div>
                        <h1 className="article-container-main-title">{RenderArticle.title}</h1>
                        <pre>{RenderArticle.body}</pre>
                    </div>
                </Fragment>
            );
        }
        
    }
    HTMLContent(){
        const openModel = this.state.openModel
        const RenderArticle = this.state.RenderArticle
        if(RenderArticle){
            return(this.ShowArticle());
        }
        if(openModel === 1){
            return(
                <Fragment>
                    {this.GenerateForm()}
                </Fragment>
            );
        }
        else{
            return(this.ShowArticles());
        }
    }

    render(){
        return(
            <Fragment>
                {this.HTMLContent()}
            </Fragment>
        );
    }
} 