import { Component,Fragment } from "react";


import '../css/loader.css'
export class Contact extends Component{
    constructor(props){
        super(props)
        this.state = {
            loaderOn:false
        }
    }
    

    async sendMail(title,body){
        let data = await fetch("http://127.0.0.1:5000/contact/sendMail",{
            method:"POST",
            body:JSON.stringify({title,body}),
            headers: new Headers({'Content-type':'application/json'})
        })
        if(data.status === 200){
            alert("Contacto exitoso!")
            return true
        }
        else{
            alert("En este momento no se puede contactar con el desarrollador :(")
            return false
        }
    }


    async ContactHandler(e){
        e.preventDefault()
        const title =  e.target[0].value
        const body = e.target[1].value
        this.setState({loaderOn:true})
        const request = await this.sendMail(title,body)
        if(request == true){
            this.setState({
                loaderOn:false
            })
            e.target.submit()
            window.location.reload()
        }
    }   
    HTMLContent(){
        if(this.state.loaderOn){
            return(
                <form onSubmit={e=>this.ContactHandler(e)} className="article-form" method="POST"> 
                    <div class="loader-container">
                        <div class="loader"></div>
                        <div class="loader-text">Enviando...</div>
                    </div>  
                    <div className="form-input-container">
                        <label htmlFor="article-title">Titulo</label>
                        <input required  type="text" name="article-title" id="article-title"/>
                    </div>
                    <div className="form-input-container">
                        <label htmlFor="article-body">Contenido</label>
                        <textarea required  id="article-body" name="article-body"></textarea>
                    </div>
                    <button>Contactar</button>
                </form>
            );
        }
        else{
            return(
                <form onSubmit={e=>this.ContactHandler(e)} className="article-form" method="POST"> 
                    <div className="form-input-container">
                        <label htmlFor="article-title">Titulo</label>
                        <input required  type="text" name="article-title" id="article-title"/>
                    </div>
                    <div className="form-input-container">
                        <label htmlFor="article-body">Contenido</label>
                        <textarea required  id="article-body" name="article-body"></textarea>
                    </div>
                    <button>Contactar</button>
                </form>
            );
        }
    }
    render(){
        return(
            this.HTMLContent()
        );
    }
}