import {Component} from "react";

import logo from "../assets/Banner/logo_udo.svg"

export class Banner extends Component{


    render(){
        return (
        <div id="banner" className="area">
            <div className="banner-svg">
                <img src={logo} alt="" width="100" height="100"/>
            </div>
            <div className="banner-content">
                <h1 id="banner-title">Organizacion y Estructura Del Computador</h1>
                <h2 className="banner-subtitle">Licenciatura en Informatica</h2>
                <h2 className="banner-subtitle">Universidad de Oriente</h2>
                <h2 className="banner-subtitle">Nueva Esparta</h2>
            </div>
            <div className="empty-space"></div>
            <div className="contact-container">
            </div>
            <ul className="circles">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
        </div>
        );
    }
}