import {Component} from "react";
import { Home } from "./Home";
import { FirstPage } from "./FirstPage";
import { SecondPage } from "./SecondPage";
import { ThirdPage } from "./ThirdPage";
import { Blog } from "./Blog";
import { Contact } from "./Contact";
import { Students } from "./Students";
export class Contenido extends Component{
    
    






   

    render(){
        console.log(this.props.page)
        if(this.props.page === 0) {return(<section className="main-content"><Home/></section>);}
        if(this.props.page === 1) {return(<section className="main-content">{<FirstPage/>}</section>);}  
        if(this.props.page === 2){return(<section className="main-content">{<SecondPage/>}</section>);}
        if(this.props.page === 3){return(<section className="main-content">{<ThirdPage/>}</section>);}
        if(this.props.page === 4){  return(<section className="main-content">{<Blog openModel={0}/>}</section>)}
        if(this.props.page === 5){return(<section className="main-content">{<Contact/>}</section>)}
        if(this.props.page === 6){return(<section className="main-content">{<Students/>}</section>)}
        
    }



}