import React,{Fragment,createRef} from "react";

import {Contenido} from './Contenido'
import { Banner } from "./Banner";
import { Footer } from "./Footer";
import '../css/model.css'
import logo from "../assets/Banner/logo_udo.svg"


import '../css/navbar.css'
import '../css/banner.css'




export class NavBar extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            page:0,
            ModelType:0,
            user:null,
        }
        this.blogRef = createRef()
    }

    


    //Manejador de paginas.
    linkHandler(e,page){
        e.preventDefault()
        
        this.setState({page:page})
        
    }


    //manejador pra el modelo de contacto
    contactHandler(e){
    }

    //renderizado.
    HTMLContent(){
        const ModelType = this.state.ModelType
        const user = JSON.parse(localStorage.getItem("currentUser"))
        if(user != null){
            return(
                <Fragment>
                    {this.OpenModel(ModelType)}
                    <div className="navbar-container">
                        <nav className="navbar">
                            <ul className="navbar-list">
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,0)} href="#" className="navbar-list-link">Home</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,1)} href="#" className="navbar-list-link">Unidad 1</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,2)} href="#" className="navbar-list-link">Unidad 2</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,3)} href="#" className="navbar-list-link">Unidad 3</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,4)} href="#" className="navbar-list-link">Articulos</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,6)} href="#" className="navbar-list-link">Estudiantes</a>
                                </li>
                                <li className="navbar-list-container empty-space"></li>
                                <li className="navbar-list-container">
                                    <button onClick={e=> this.linkHandler(e,5)} id="contact-button">Contactar</button>
                                </li>
                                <li className="navbar-list-container">
                                    <a  href="#" className="navbar-list-link">{user.username}</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.LogoutHandler()} href="#" className="navbar-list-link">Salir</a>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </Fragment>
            );
        }
        else{
            return(
                <Fragment>
                    {this.OpenModel(ModelType)}
                    <div className="navbar-container">
                        <nav className="navbar">
                            <ul className="navbar-list">
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,0)} href="#" className="navbar-list-link">Home</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,1)} href="#" className="navbar-list-link">Unidad 1</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,2)} href="#" className="navbar-list-link">Unidad 2</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,3)} href="#" className="navbar-list-link">Unidad 3</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,4)} href="#" className="navbar-list-link">Articulos</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.linkHandler(e,6)} href="#" className="navbar-list-link">Estudiantes</a>
                                </li>
                                <li className="navbar-list-container empty-space"></li>
                                <li className="navbar-list-container">
                                    <button onClick={e=> this.linkHandler(e,5)} id="contact-button">Contactar</button>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.HandleOpenModelClick(e,2)} href="#" className="navbar-list-link">Registrarse</a>
                                </li>
                                <li className="navbar-list-container">
                                    <a onClick={e => this.HandleOpenModelClick(e,1)} href="#" className="navbar-list-link">Entrar</a>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </Fragment>
            );
        }
        
        
    }


    //manejado de cerrado sesion.
    LogoutHandler(){
        localStorage.removeItem("currentUser")
        this.setState({user:null,page:0})
    }
   
    //Methods to login and register an user.
    




    //login methods

    async Login(username,password){
        let data = await this.FetchUser(username,password)
        if(data.status === 200){
            data = await data.json()
            localStorage.setItem("currentUser",JSON.stringify(data))
            this.setState({user:data,ModelType:0})
        }
        else{
            alert("Usuario invalido")
        }
    }

    async FetchUser(username,password){
        let data = await fetch("http://localhost:5000/user/login",{
            method:"POST",
            cache:'no-cache',
            body:JSON.stringify({username,password}),
            headers:new Headers({'Content-Type':'application/json'})
        })
        return data
    }


    //register methods

    async Register(username,password,email){
        let data = await this.RegisterUser(username,password,email)
        if(data.status === 200){
            alert("Registrado exitosamente!")
        }
        else{
            alert("El usuario ya existe!")
        }
    }


    async RegisterUser(username,password,email){
        let data = await fetch("http://localhost:5000/user/register",{
            method:"POST",
            cache:'no-cache',
            body:JSON.stringify({username,password,email}),
            headers:new Headers({'Content-Type':'application/json'})
        })
        return data
    }
    async FormPreventDefault(e,typeOfSubmit){
        e.preventDefault()
        
        if(typeOfSubmit == "Login"){
            const username = e.target[1]
            const password = e.target[2]
            await this.Login(username.value,password.value)
            username.value = ""            
            password.value = "" 
        }
        else if(typeOfSubmit == "Register"){
            const email = e.target[1]
            const username = e.target[2]
            const password = e.target[3]
            await this.Register(username.value,password.value,email.value)
            email.value = ""            
            username.value = ""            
            password.value = ""            
        }

        
    }


    //Prevenir el cierre del model al presionar enter.
    onKeyHandler(e){
        if(e.key === "Enter"){
            e.preventDefault()
        }
    }



    //Methods  to manipulate the models.


    HandleOpenModelClick(e,ModelType){
        this.setState({ModelType:ModelType})
    }
    CloseButtonHandler(){
        this.setState({
            ModelType:0
        })
    }

    OpenModel(ModelType){
        if(ModelType == 1){
            return(this.LogInModelTemplate());
        }
        else if(ModelType == 2){
            return(this.RegisterTemplate());
        }
    }
    
    
    

    LogInModelTemplate(){
        return(
            <div className="modal-container">
                <div  className="modal">
                    <form onSubmit={e => this.FormPreventDefault(e,"Login")} onKeyDown={e => this.onKeyHandler(e)} method="POST">
                        <div className="form-header-box">
                            <img src={logo} alt="" width="32" height="32"/>
                            <header className="form-header">Ingresar</header>
                            <button onClick={e => this.CloseButtonHandler()} className="form-header-close-button">X</button>
                        </div>
                        <div className="input-container">
                            <label htmlFor="username">Usuario</label>
                            <input type="text" name="username" id="username" required/>
                        </div>
                        <div className="input-container">
                            <label htmlFor="password" >Contraseña</label>
                            <input type="password" name="password" id="password" required/>
                        </div>
                        <div className="input-container">
                            <button type="submit">Ingresar</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
    RegisterTemplate(){
        return(
            <div className="modal-container">
                <div  className="modal">
                    <form onSubmit={e => this.FormPreventDefault(e,"Register")} onKeyDown={e => this.onKeyHandler(e)}  method="POST">
                        <div className="form-header-box">
                            <img src={logo} alt="" width="32" height="32"/>
                            <header className="form-header">Registrarse</header>
                            <button onClick={e => this.CloseButtonHandler()} className="form-header-close-button">X</button>
                        </div>
                        <div className="input-container">
                            <label htmlFor="email">Correo Electronico</label>
                            <input type="email" name="email" id="email" required/>
                        </div>
                        <div className="input-container">
                            <label htmlFor="username">Usuario</label>
                            <input type="text" name="username" id="username" required/>
                        </div>
                        <div className="input-container">
                            <label htmlFor="password" >Contraseña</label>
                            <input type="password" name="password" id="password" required/>
                        </div>
                        <div className="input-container">
                            <button type="submit">Registrarse</button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }









    render(){
        const page = this.state.page
        if(page === 0){
            return(
                <Fragment>
                    {this.HTMLContent()}
                    <Contenido ref={this.blogRef} page={page}/>
                    <Footer/>
                </Fragment>
            );
        }
        return(
            <Fragment>
                {this.HTMLContent()}
                <Banner/>
                <Contenido ref={this.blogRef} page={page}/>
                <Footer/>
            </Fragment>
        );
    }

}