import { Component,Fragment } from "react";

export class ThirdPage extends Component{



    HTMLContent(){
        return(
            <Fragment>
                <h1 className="main-content-header">ORGANIZACION DE LOS SISTEMAS DE LAS COMPUTADORAS</h1>
                <section className="main-content-section">
                    <h1 className="main-content-title">Organizacion del computador</h1>
                    <p className="main-content-paragraph">
                        Se refiere a todas aquellas unidades funcionales que debe tener todo computador para funcionar.
                    </p>
                    <h1 className="main-content-title">Unidades Funcionales</h1>
                    <p className="main-content-paragraph">
                        Cuando nos referimos a unidades funcionales hablamos de: CPU (Central Process Unit),
                        Memoria Principal y las unidades de entrada y salida, se les llama unidades funcionales, porque cumplen
                        una funcion especifica que ninguna de las otras unidades puede cumplir.
                    </p>
                    <h1 className="main-content-title">Arquitectura del computador</h1>
                    <p className="main-content-paragraph">
                        Se refiere a las instrucciones a bajo nivel, es decir, el lenguaje de maquina y todo aquello que el programador
                        puede observar y manejar de estas instrucciones a bajo nivel en tiempo de ejecucion.
                        Asi se toman en cuenta por ejemplo la cantidad de bits para representar las instrucciones, el formato
                        de instruccion de la maquina o el modo direccionamiento.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/ThirdPage/CPU.png")} alt=""/>
                        <img src={require("../assets/ThirdPage/RAM.jpg")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Unidad Central de Procesamiento (UCP)</h1>
                    <p className="main-content-paragraph">
                        Es la que se encarga de procesar todas las instrucciones, se considera
                        el cerebro del computador, captura, codifica y ejecuta cada instruccion
                        para llevar a cabo sus tareas, las divide en dos unidades mas
                        Unidad de Control y Unidad Aritmetico Logica (UAL)
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/ThirdPage/CPU.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Unidad de Control</h1>
                    <p className="main-content-paragraph">
                        Es conocida como el director del computador, es el que se encarga de coordinar
                        la ejecucion de la instrucciones es quien determina una vez capturadas como se ejecutaran
                        y como resultado dara una serie de ordenes a los elementos separados del computador.
                    </p>
                    <h1 className="main-content-title">Registro</h1>
                    <p className="main-content-paragraph">
                        Es una zona de almacenamiento temporal, es decir, que al momento de apagar el computador, se pierde la informacion
                        que se encuentra en ellos, los registros suelen ser todos del mismo tamaño.
                    </p>
                    <h1 className="main-content-title">Señales que interviene en la Unidad de Control</h1>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Reloj</strong>: Es un circuito que marca el periodo en el que se debe dar un cambio de fase
                            para la instrucción el periodo es un determinado tiempo del que se dispone
                            para la fase de búsqueda o ejecución de la instrucción, mientras mas rápido marque
                            el reloj, mas rápido será la ejecución de las instrucciones.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Interprete</strong>: Es un circuito que se encarga de decodificar la instruccion que se encuentra
                            en el registro de instrucciones en el momento.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Registro de Estado</strong>: Es el que guarda los indicadores de estado, es decir, una serie de informacion
                            que constituye un registro historico de las operaciones precedentes, cada registro de estado se puede consultar por separado.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Registro contador de programas</strong>: Almacena una direccion de la siguiente instruccion en secuencia que se debe ejecutar.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Registro instruccion</strong>: Guarda la instruccion que esta siendo decodifica por el interprete.
                        </li>
                    </ul>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Unidad Aritmetico Logica</h1>
                    <p className="main-content-paragraph">
                        Es el circuito que se encarga de realizar operaciones aritmeticas y logicas basicas, entre ellas: 
                        La suma, resta, multiplicacion y division, ademas tambien realiza operaciones logicas como: AND, OR  y NOT. Esta unidad
                        solo trabaja con valores enteros. Existe una unidad muy similar a esta que hace operaciones con numeros flotantes o en punto decimal
                        esta unidad es llamada FPU (Flotant Point Unit).
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/ThirdPage/ejemplo_ual.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Memoria Principal</h1>
                    <p className="main-content-paragraph">
                    La memoria principal es la memoria que se encarga de almacenar tantos los datos como los
                    programas que la CPU esta procesando en un determinado momento. Por su función, la MP,
                    debe ser inseparable del microprocesador o CPU, con quien se comunica atra vez del bus de
                    datos y el bus de direcciones.
                    </p>
                    <p className="main-content-paragraph">
                        Esta estructura por un conjunto de filas y columnas, cada una de las identifica por una palabra de memoria
                        cada una de las divisiones es una celda, localidad o byte.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/ThirdPage/memoria_principal.jpg")} alt=""/>
                        <img src={require("../assets/ThirdPage/ejemplo_memoria_principal.png")} alt=""/>
                    </div>
                    <p className="main-content-paragraph">
                        Algunos elementos que forman parte de la memoria principal son:
                    </p>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Registro intermedio de memoria</strong>: Es aquel donde se guarda el dato o la instruccion que se va a escribir en la memoria, antes de que este pase a la UCP
                        </li>
                        <li className="main-content-list-item">
                            <strong>Registro de direccion de memoria</strong>: Es donde se encuentra la direccion de la palabra a la que se va a acceder.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Selector de memoria</strong>: Es un circuito que pone en comunicacion a ambos registros y coordina
                            el trabajo interno enesa memoria principal.
                        </li>
                    </ul>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Unidades de entrada y salida</h1>
                    <p className="main-content-paragraph">
                        Son los que coordinan la interaccion o comunicacion entre la UCP y los perifericos
                        (no confundir las unidades de entrada y salida con los perifericos).
                        Un periferico puede ser una impresora, scanner, mouse o teclado y quien permite la conexion
                        entre el UCP y el periferico es la unidad de entrada y salida, ejemplo los puertos USB del computador.
                    </p>
                    <h1 className="main-content-title">Formas de comunicacion de las unidades con la UCP</h1>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Entrada/Salida Programada</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Entrada/Salida por Interrupciones</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Acceso directo a memoria</strong>
                        </li>
                    </ul>
                    <div className="section-img-container">
                        <img src={require("../assets/ThirdPage/entrada_salida.jpg")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Buses del Sistema</h1>
                    <p className="main-content-paragraph">
                        Son lineas de interconexion entre las unidades funcionales del computador, son lineas en la placa del circuito
                        impreso de la computadora. Segun el tipo de informacion que viaja por ellos se clasifican
                    </p>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Bus de Datos</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Bus de Direcciones</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Bus de Control</strong>
                        </li>
                    </ul>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Que es una instruccion?</h1>
                    <p className="main-content-paragraph">
                        Una instruccion en informatica se refiere al conjunto de datos insertados en una secuencia estructura
                        o especifica que el procesador interpreta y ejecuta a bajo nivel estas instrucciones son una secuencia de ceroes y unos.
                        La instruccion esta estructura en dos partes:
                    </p>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Campo codigo de operacion (CCO)</strong>:
                            Es el que determina cual es la accion que se realizara, es el que al leer la UCP sabe cual es la tarea que debe llevar a cabo.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Campo codigo operando</strong>:
                            Es el que indica donde estan los valores con los que se realizara la operacion y donde se almacenara el resultado.
                        </li>
                    </ul>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Formatos de instruccion</h1>
                    <p className="main-content-paragraph">
                        Se refiere a la organizacion de los bits dentro de las instrucciones de manera que la UCP los pueda leer
                        o interpretar.
                    </p>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Formato de una palabra de MP</strong>: Significa que toda la instruccion se guara en una sola palabra
                            de memoria.
                        </li>
                        <li className="main-content-list-item">
                            <strong>Formato de N palabras de MP</strong>:
                            Es decir que toda la instruccion ocupa mas de una palabra.
                        </li>
                    </ul>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Modos de direccionamiento</h1>
                    <p className="main-content-paragraph">
                        Son los que le dicen a la UCP como interpretar el campo de direccion de la instruccion.
                    </p>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">
                            <strong>Implicito</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Inmediato</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Directo</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Indirecto</strong>
                        </li>
                        <li className="main-content-list-item">
                            <strong>Indexado</strong>
                        </li>
                    </ul>
                </section>
            </Fragment>
        );
    }

    render(){
        return(
            this.HTMLContent()
        );
    }
}