import { Component,Fragment } from "react";
import '../css/testimony.css'


export class Students extends Component{



    HTMLContent(){
        return(
            <div className="box">
                <div className="box-content">
                    <div className="box-image">
                        <img src={require("../assets/Students/raziel.jpg")}/>
                        <h1 className="box-img-text">Jesus Raziel</h1>
                    </div>
                    <div className="box-container-content">
                        <p className="box-text">
                        El material fue bastante completo, y nos cubrieron las deficiencias de lógica de circuitos que tuvimos en materias anteriores.
                        Fue muy interesante ver cómo funciona un computador a bajo nivel, y el enfoque teórico de las primeras unidades fue si no
                        esperado, bienvenido. Margarita es una profesora que se explica muy bien, y tiene gran dedicación a la enseñanza. Espero
                        cursar alguna materia con ella de nuevo en el futuro.
                        </p>
                    </div>
                </div>
                <div className="box-content">
                    <div className="box-image">
                        <img src={require("../assets/Students/kevin.png")}/>
                        <h1 className="box-img-text">Kevin Rojas</h1>
                    </div>
                    <div className="box-container-content">
                        <p className="box-text">
                        El temario de la materia Organización y Estructura del Computador fue bastante extenso y explicado perfectamente por la
                        profesora. Esta materia hizo que me diera cuenta de lo atrayente que es el hardware de los computadores y como las mentes
                        brillantes del siglo XIX se esmeraron en digitalizar el mundo.
                        Sin duda mi materia favorita del cuarto semestre.
                        </p>
                    </div>
                </div>
            </div>
        );
    }

    render(){

        return(
            this.HTMLContent()
        );
    }
}