import { Component,Fragment } from "react";
import '../css/content.css'
export class FirstPage extends Component{



    HTMLContent(){
        return(
            <Fragment>
                <h1 className="main-content-header">JERARQUIA DE MAQUINAS VIRTUALES O MULTINIVEL</h1>
                <section className="main-content-section">
                    <h1 className="main-content-title">Organizacion Estructurada de Computadoras</h1>
                    <p className="main-content-paragraph">Consiste en estructurar el computador como una serie de abstracciones, donde cada una se apoya por la que esta encima de la otra
                        haciendo posible disminuir o controlar la complejidad y diseñar sistemas de manera organizada.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/website-building-1.png")} alt=""/>
                        <img src={require("../assets/FirstPage/inteligencia-artificial.png")} alt=""/>
                    </div>
                    <h1 className="main-content-title">Lenguajes, niveles y maquinas virtuales</h1>
                    <p className="main-content-paragraph">Cuando se habla de lenguajes, hablamos de un conjunto de instrucciones, cuyo nivel de complejida esta asociado al mismo.
                        El nivel 0, es un nivel bastante simple, pero que no es facil de comprender por el ser humano, de esta forma se van generando nuevos niveles,
                        existe el lenguaje (l1), que este se basa en el lenguaje 0 (l0), ademas cada lenguaje es asociado  a una maquina virtual del mismo nivel,
                        existe la maquina real (m0), asociado al lenguaje de maquina (l0), la maquina virtual 1 (m1) y el lenguaje 1 (l1). Se dice que son maquinas virtuales,
                        porque son aplicaciones escritas por personas en el lenguaje especificio, que se ejecuta dentro de la maquina.
                        Es importante acotar que el lenguaje 1, no puede ser muy diferente del lenguaje 0 (lenguaje maquina), esto conlleva a la creacion de un nuevo conjunto de instrucciones
                        en el nivel 2, que como se menciono al inicio, es una abstraccion del nivel anterior, este proceso es llevado a cabo cuantas veces sea necesario para crear un lenguaje
                        que sea ideal para las aplicaciones. Ahora bien para llevar a cabo la ejecucion de instrucciones de L1 en L0, se pueden utilizar dos tecnicas: Traduccion y Compilacion.
                    </p>
                    <h1 className="main-content-title">Traduccion (Compilacion) </h1>
                    <p className="main-content-paragraph">Es un proceso en el cual se toman todas las instrucciones escritas en el lenguaje L1, se crea la misma aplicacion pero con instrucciones en L0
                        y luego es ejecutado en L1
                    </p>
                    <h1 className="main-content-title">Interpretacion</h1>
                    <p className="main-content-paragraph">Es un proceso en el cual en tiempo de ejecucion, se van aplicando la instrucciones 1 por 1, el proceso de interpretacion, se lleva a cabo por un interprete
                        este interprete esta escrito en el lenguaje L0
                    </p>
                    <div className="section-img-container">
                        <img style={{backgroundColor: 'white'}} src={require("../assets/FirstPage/code-to-machine.png")} alt=""/>
                        <img src={require("../assets/FirstPage/concepto-ilustracion-isometrica-reconocer-lenguaje-programacion_18660-1790.jpg")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Maquinas multinivel contemporaneas</h1>
                <p className="main-content-paragraph">Estas maquinas cuentan con 6 niveles, incluyendo el 0.</p>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Logica Digital (0)</h1>
                    <p className="main-content-paragraph">En este nivel se habla principalmente de la parte fisica del computador, el hardware del mismo, 
                        donde los objetos intengrantes son las compuertas logicas, tambien llamadas puertas logicas.
                    </p>
                    <h1 className="main-content-title">Compuertas Logicas</h1>
                    <p className="main-content-paragraph">Una compuerta logica, es un pequeño circuito integrado en una pastilla
                        que responde desde el punto de vista logico a una funcion basica, estas funciones pueden ser:
                        AND, OR, NOT.
                    </p>
                    <p className="main-content-paragraph">En el caso de la compuerta AND, contiene unicamente dos valores de entrada y una sola salida,
                        en los valores de entrada solamente puede haber un 0 o un 1, y en la salida de igual forma, para que la salida sea un 1, ambos valores ingresados
                        deben contener un 1.
                    </p>
                    <p className="main-content-paragraph">La compuerta OR, al igual que la compuerta AND, recibe unicamente dos valores como entrada y una unica salida, la diferencia es que
                        requiere una entrada con valor de 1, para que su salida sea 1.
                    </p>
                    <p className="main-content-paragraph">La compuerta NOT contiene un unico valor de entrada y una unica salida, con el detalle de que la salida esta compuesta por el complemento
                        de su valor de entrada.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/compuerta-logica-and-de-2-entradas1-1.jpg")} alt=""/>
                        <img src={require("../assets/FirstPage/compuerta-logica-or-2-de-entradas.jpg")} alt=""/>
                        <img src={require("../assets/FirstPage/compuerta-logica-not-simbolo-y-tabla-de-verdad.jpg")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Microarquitectura (1)</h1>
                    <p className="main-content-paragraph">Este nivel esta compuesto por tres elementos: Banco de Registros, Unidad Aritmetico Logica (UAL) y 
                        Logica de Control
                    </p>
                    <h1 className="main-content-title">Banco de Registros</h1>
                    <p className="main-content-paragraph">Es una coleccion de registros que forman una memoria local, pueden ser registro de 8 a 32 todos de un bit.,
                        se dice que es una memoria local porque se encuentra muy cercana al procesador y esto que hace que el acceso a la memoria sea mucho mas rapido,
                        estos registros son de uso general, es decir, pueden ser instrucciones, operaciones o datos.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/banco_registros.jpg")} alt=""/>
                        <img src={require("../assets/FirstPage/banco_registro_8.jpg")} alt=""/>
                    </div>
                    <h1 className="main-content-title">Unidad Aritmetico Logica (UAL)</h1>
                    <p className="main-content-paragraph">Es un circuito que puede realizar operaciones arimeticas y logicas basicas,
                        esto quiere decir que realiza operaciones aritmeticas como: Suma, Resta, Multiplicacion y Division y Logicas como:
                        AND, OR y NOT.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/UAL.jpg")} alt=""/>
                    </div>
                    <h1 className="main-content-title">Logica de Control</h1>
                    <p className="main-content-paragraph">Esta se encarga de guiar, por decirlo de una forma, la unidad aritmetico logica y el banco de registros.</p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/Funciones lógicas para control (1).png")} alt=""/>
                    </div>
                    <h1 className="main-content-title">CPU (Central Process Unit) Microprogramada</h1>
                    <p className="main-content-paragraph">En algunas maquinas anteriormente, contenian un elemento llamado 'microprograma',
                        que se encargaba de controlar la operacion de trayectoria de datos, este era un interprete de la operaciones de nivel 2,
                        que obtiene, decodifica y ejecuta las instrucciones.
                    </p>
                    <p className="main-content-paragraph">Los procesadores microprogramados contenian una pequeña memoria que guarda las secuencias de microinstrucciones
                        utilizadas para implementar cada instruccion, pues ellos accedian a dicha memoria, con el fin de encontrar el conjunto de microinstrucciones
                        necesarias para implementar esa unica instruccion.
                    </p>
                    <p className="main-content-paragraph">La microprogramacion era empleada en los primeros computadores donde la tecnologia para su construccion limitaba
                        mucho el tamaño del hardware debido al tamaño de los componentes, que estos se constituan por tubos de vacios y transistores discretos.
                    </p>
                    <h1 className="main-content-title">CPU (Central Process Unit) Cableada</h1>
                    <p className="main-content-paragraph">Implementa las instrucciones dentro del propio hardware, a diferencia de la CPU microprogramada, que es mas lenta
                        debido a que esta debia acceder a la memoria de microinstrucciones para poder ejecutar sus instrucciones.
                        Gracias al avance de la tecnologia VLSI (Very Large Scale Integration) que consiste en un proceso de creacion de circuitos
                        integrados por combinaciones de cientos de circuitos basados en transistores en un solo chip. Actualmente existen tres tipos:
                        Tecnologia VLSI (Very Large Scale Integration) que integra entre 10.000 y 99.999 circuitos en un solo chip,
                        Tecnologia ULSI (Ultra Large Scale Integration) que integra entre 100.000 y 999.999 circuitos en un solo chip y 
                        la Tecnologia GSI (Giga Scale Integration) que integra mas de 1.000.000 de circuitos en un solo chip.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/CPU.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Arquitectura del Conjunto de Instrucciones ISA (2)</h1>
                    <p className="main-content-paragraph">Se refiere a un conjunto de instrucciones que generalmente se describen en el manual de referencia de un computador
                        son las instrucciones que el microprograma o los circuitos de ejecucion del hardware, ejecutan de forma interpretativa, depende mucho de la arquitectura de la CPU
                    </p>
                    <h1 className="main-content-title">Aspectos que han influenciando en la variacion de este nivel</h1>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">Menor uso del lenguaje ensamblador</li>
                        <li className="main-content-list-item">Garantiza la compatibilidad</li>
                        <li className="main-content-list-item">Ejecucion de Instrucciones ISA por el hardware</li>
                    </ul>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/descarga.png")} alt=""/>
                        <img src={require("../assets/FirstPage/imagen_post_comp-1.png")} alt=""/>
                    </div>
                    <h1 className="main-content-title">Aspectos Importantes</h1>
                    <ul className="main-content-list">
                        <li className="main-content-list-item">Debate RISC vs CISC</li>
                        <li className="main-content-list-item">Seleccion del modo de direccionamiento</li>
                        <li className="main-content-list-item">Formato de instruccion fija o variable</li>
                        <li className="main-content-list-item">Instrucciones vectoriales para procesamiento de multimedia</li>
                    </ul>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/dispositivo-multimedia-procesamiento-3d_10376-132.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Sistema Operativo (3)</h1>
                    <p className="main-content-paragraph">En este nivel podemos encontrar instrucciones que estan en el nivel de ISA
                        adicionalmente hay un nuevo conjunto de instrucciones, relacionados directamente con el diseño del sistema operativo,
                        como pueden ser la organizacion de la memoria, la capacidad de ejecutar 1 o mas programas al mismo tiempo.
                        Hay una distinta variacion de diseños en este nivel, como pueden ser: Ubuntu, IOS, Windows, MAC OS o Android.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/Ubuntu-Logo-Transparent-Images.png")} alt=""/>
                        <img style={{backgroundColor: 'white'}} src={require("../assets/FirstPage/770df-windows_xp.png")} alt=""/>
                        <img src={require("../assets/FirstPage/android-data-recovery-tr5.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Lenguaje Ensamblador (4)</h1>
                    <p className="main-content-paragraph">Ofrece un metodo para escribir programas para los niveles 1, 2 y 3
                        de forma mas comprensible, los programas en lenguaje ensamblador primero se traducen a un lenguaje 1, 2 o 3
                        y luego se interpretan por la maquina virtual o real apropiada, el programa que realiza la traduccion, se llama tambien
                        ensamblador.
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/5c462362fffaf041ad0d882a69b70187.png")} alt=""/>
                    </div>
                </section>
                <section className="main-content-section">
                    <h1 className="main-content-title">Nivel de Lenguajes Orientados a Problemas (5)</h1>
                    <p className="main-content-paragraph">Consta de los lenguajes diseñados para ser utilizados por los programadores de aplicaciones
                        que quieren resolver problemas, estos lenguajes se denominan lenguajes de alto nivel y existen un monton de ellos
                        los programas escritos en estos lenguajes generalmente se traducen a lenguajes de nivel 3 o 4, llamados compiladores, aunque tambien
                        se interpretan en lugar de traducirse (compilarse).
                    </p>
                    <div className="section-img-container">
                        <img src={require("../assets/FirstPage/JavaScript_logo.png")} alt=""/>
                        <img src={require("../assets/FirstPage/Python-Symbol.png")} alt=""/>
                    </div>
                </section>
            </Fragment>
        );
    }

    render(){
        return(
            this.HTMLContent()
        );
    }
}