import { Component } from "react";
import '../css/home.css'
import '../css/animation.css'

import facebook_logo from "../assets/Home/facebook_logo.svg"
import linkedin_logo from "../assets/Home/linkedin_logo.svg"
import github_logo from "../assets/Home/github_logo.svg"
import logo from "../assets/Banner/logo_udo.svg"
export class Home extends Component{
    
    HTMLContent(){
        return(
            <section id="content">
                <div className="bg" >
                    <ul className="dots">
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                    </ul>
                </div >
                <div id="header">
                    <img id="header-svg" src={logo} alt="https://iconape.com/udo-logo-logo-icon-svg-png.html" width="80" height="80"/>
                    <h1 id="header-title">Universidad De Oriente</h1>
                    <h2 className="header-subtitle">Licenciatura en Informatica</h2>
                    <h2 className="header-subtitle">Nueva Esparta</h2>
                </div>
                <div id="body">
                    <h1 id="body-title">Organizacion y Estructura Del Computador</h1>
                    <p id="body-content">Materia que estudia los elementos que conforman y estructuran el computador
                    </p>
                    <ul className="media-list">
                        <li className="media-list-container">
                            <a href="https://facebook.com/jysus.figuera" className="media-list-link">
                                <img src={facebook_logo} alt="https://iconos8.es/icons/set/facebook" width="48" height="48"/>
                            </a>
                        </li>
                        <li className="media-list-container">
                            <a href="https://www.linkedin.com/in/jesus-figuera-b200aa184/" className="media-list-link">
                                <img src={linkedin_logo} alt="https://iconos8.es/icons/set/facebook" width="48" height="48"/>
                            </a>
                        </li>
                        <li className="media-list-container">
                            <a href="https://github.com/Sentineldev" className="media-list-link">
                                <img src={github_logo} alt="https://iconos8.es/icons/set/facebook" width="48" height="48"/>
                            </a>
                        </li>
                    </ul>
                </div>
            </section>
        );
    }

    render(){
        return(this.HTMLContent());
    }
}